export default {
	name: "emModal",
	props: {
		//是否显示
		show: {
			type: Boolean,
			default: false
		},
		//标题
		title: {
			type: String,
			default: ""
		},
		//内容
		content: {
			type: String,
			default: ""
		},
		//内容字体颜色
		color: {
			type: String,
			default: "#999"
		},
		//内容字体大小
		size: {
			type: Number,
			default: 28
		},
		//形状 circle, square
		shape: {
			type: String,
			default: 'square'
		},
		button: {
			type: Array,
			default: function() {
				return [{
					text: "取消",
					type: "red",
					plain: true //是否空心
				}, {
					text: "确定",
					type: "red",
					plain: false
				}]
			}
		},
		//点击遮罩 是否可关闭
		maskClosable: {
			type: Boolean,
			default: true
		},
		//自定义弹窗内容
		custom: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {

		};
	},
	methods: {
		handleClick(e) {
			if (!this.show) return;
			const dataset = e.currentTarget.dataset;
			this.$emit('click', {
				index: Number(dataset.index)
			});
		},
		handleClickCancel() {
			if (!this.maskClosable) return;
			this.$emit('cancel');
		},
		px(num) {
			return uni.upx2px(num) + 'px'
		}
	},
	mounted() {
	}
}
